nav {
  padding: 0 4vw;
  position: sticky;
  top: 0;
  min-height: 6.5vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #141414;
  color: white;
  z-index: 3;
}

.logo {
  color: rgb(229, 9, 20);
  font-family: "Bebas Neue", cursive;
  letter-spacing: 2px;
  font-weight: lighter;
  font-size: 1.5rem;

  h1 {
    font-weight: 100;
    cursor: pointer;
  }
}

.nav-links {
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: 20%;
  margin-left: 3rem;

  li {
    font-size: 0.9rem;
    cursor: not-allowed;
  }
  @media (max-width: 1490px) {
    width: 30%;
  }
  @media (max-width: 1000px) {
    width: 40%;
  }
  @media (max-width: 750px) {
    width: 50%;
  }
  @media (max-width: 650px) {
    display: none;
  }
}
