.card-shadow {
  width: 100%;
  min-height: 100vh;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
}

.movie-overview {
  width: 50vw;
  height: 100vh;
  background-color: #181818;
  position: absolute;
  left: 25%;
  color: black;
  margin-top: 3vh;
  z-index: 10;
  img {
    width: 100%;
    height: 50vh;
    border-radius: 0.5rem 0.5rem 0rem 0rem;
    box-shadow: 0 8px 6px -6px black;
  }
  h3 {
    margin: 0rem 2rem 0rem 2rem;
    padding-left: 0;
  }
  .movie-year-time {
    display: flex;
    margin-bottom: 1rem;
    h4 {
      margin: 0rem 2rem 0rem 2rem;
      color: white;
      font-weight: normal;
    }
  }
  @media (max-width: 1240px) {
    width: 75vw;
    left: 12.5%;
  }
  @media (max-width: 750px) {
    h3 {
      font-size: 1.3rem;
    }
    img {
      height: 40vh;
    }
  }
  @media (max-width: 600px) {
    h3 {
      font-size: 1.2rem;
    }
    img {
      height: 30vh;
    }
  }
  @media (max-width: 500px) {
    h3 {
      font-size: 1rem;
    }
    img {
      height: 20vh;
    }
  }
}

.movie-detail {
  margin: 0rem 2rem 0rem 2rem;
  display: flex;
  p {
    text-align: justify;
  }
  .movie-genre {
    width: 25%;
    p {
      padding-left: 1rem;
      font-size: 1rem;
    }
  }

  @media (max-width: 850px) {
    p {
      font-size: 1rem;
    }
    .movie-genre {
      p {
        font-size: 1rem;
      }
    }
  }
  @media (max-width: 550px) {
    flex-direction: column;
    margin: 0rem 0.5rem 0rem 0.5rem;
    p {
      font-size: 0.9rem;
    }
    .movie-genre {
      width: 100%;
      p {
        padding-left: 0rem;
        font-size: 0.9rem;
      }
    }
  }
}
